  /* Position the logo image in the top-left corner with padding */
  .img-fluid.logo-img.lazyloaded {
    position: absolute;
    top: 25px;
    left: 25px;
  }

   
  
  /* Make the large image responsive */
  .img-fluid.logo-fix.lazyloaded {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: 500px; /* Set a reasonable max width */
  }
  
  /* Responsive design for different screen sizes */
  @media (max-width: 768px) {
    /* Adjust the top-left logo for smaller screens */
    .img-fluid.logo-img.lazyloaded {
      top: 15px;
      left: 15px;
      width: 60px; /* Resize the image for smaller screens */
    }
  
    /* Adjust the small image size for smaller screens */
    .img-fluid.lazyloaded {
      width: 100%; /* Smaller on mobile devices */
    }
  
    /* Adjust the large image for smaller screens */
    .img-fluid.logo-fix.lazyloaded {
      max-width: 300px; /* Reduce the max-width on smaller screens */
    }
  }
  
  @media (max-width: 480px) {
    /* Further adjustments for very small screens */
    .img-fluid.logo-img.lazyloaded {
      top: 10px;
      left: 10px;
      width: 50px; /* Resize for very small screens */
    }
  
    .img-fluid.lazyloaded {
      width: 100%; /* Very small on tiny screens */
    }
  
    .img-fluid.logo-fix.lazyloaded {
      max-width: 250px; /* Further reduce the max-width */
    }
  }
  
